import React, { Fragment } from 'react';

import { Link } from 'react-router-dom';
import 'react-perfect-scrollbar/dist/css/styles.css';
import PerfectScrollbar from 'react-perfect-scrollbar';
const Sidenav = props => {
  const getStyle = () => {
    const { showNav } = props;
    let styles = {
      overlay: {
        position: 'absolute',
        width: '100%',
        height: '100%',
        top: 0,
        left: 0,
        opacity: showNav ? 1 : 0,

        transition: 'opacity 0.3s cubic-bezier(0, 0, 0.3, 1)',
        willChange: 'opacity'
      },
      nav: {
        transform: showNav ? 'translateX(0%)' : `translateX(-100%)`,
        transition: `transform ${
          showNav ? '0.33s' : '0.13s'
        } cubic-bezier(0,0,0.3,1)`
      }
    };

    return styles;
  };
  return (
    <Fragment>
      <div style={getStyle().overlay} onClick={props.onHideNav} />
      <div className="side-nav sn-bg-4 fixed" style={getStyle().nav}>
        <PerfectScrollbar>
          <ul style={{ maxHeight: '100vh' }}>
            <li className="logo-sn waves-effect">
              <div className="text-center">
                <Link to="#" className="pl-0">
                  <img src={props.logo} className="" alt="logo" />
                </Link>
              </div>
            </li>
            <li>
              <form className="search-form" role="search">
                <div className="form-group md-form mt-0 pt-1 waves-light waves-effect waves-light">
                  <input
                    type="text"
                    className="form-control"
                    placeholder="Search"
                  />
                </div>
              </form>
            </li>
            {props.children}
          </ul>
        </PerfectScrollbar>
        <div className="sidenav-bg mask-strong" />
      </div>
    </Fragment>
  );
};
export default Sidenav;
